import React from 'react';
import "./index.css";

function Resume(props) {
    return (
      <div className="section Resume" id="section_Resume">
        <h1>Resume {props.name}</h1>

        <br/>
        <a href="https://docs.google.com/document/d/19YhPPEc8Uy9ymPxDxLDfTkoQJgtnnVJNVR9J5nAu5DA/edit?usp=sharing">Click here to download my resume</a>

        <br/>
        <h3>Developer Proficiencies</h3>

        <ul className="proeficiencies">
          <li>Javascript</li>
          <li>NodeJS</li>
          <li>SQL</li>
          <li>React</li>
        </ul> 
      </div>
    );
  };

export default Resume;